import { useState } from 'react';
import { FloatButton, Input, message, Modal, Tooltip } from 'antd';
import { PlusOutlined } from '@ant-design/icons';

import { happyService } from 'src/constants';
import { isUrl, getCookieDomain, setLocalProxy, getLocalProxy } from '../utils';
import type { LocalProxyItem } from '../types';

const AddProxyModal = ({ onOkCb }: { onOkCb: () => void }) => {
  const [visible, setVisible] = useState(false);
  const [obj, setObj] = useState<LocalProxyItem>({
    original: '',
    target: '',
    tags: [],
    open: true,
    cookiesMap: {}
  });

  const validateUniq = (): boolean => {
    const local = getLocalProxy();
    if (local.find((it) => it.original === obj.original)) {
      message.error('');
      return false;
    }
    return true;
  };

  const valiteEmpty = () => {
    if (!obj.original) {
      message.error('');
      return false;
    }
    return true;
  };

  const onOk = () => {
    if (!valiteEmpty() || !validateUniq()) {
      return;
    }

    // 
    const local = getLocalProxy();
    // 
    const newLocal = [...local, obj];
    setLocalProxy(newLocal);
    setVisible(false);

    // 
    onOkCb?.();
  };

  const onChange = (e: any) => {
    const original = e.target.value;

    const originIsUrl = isUrl(original);
    const theOrigin = originIsUrl ? new URL(original).origin : '';

    const target =
      originIsUrl && theOrigin
        ? original.replace(theOrigin, happyService)
        : `${happyService}/${original}`;

    if (chrome.cookies.getAll) {
      const cookieDomain = getCookieDomain(original);
      chrome.cookies.getAll({ domain: cookieDomain }, (resArr) => {
        // TODO: cookiehttp-only
        const cookiesStr = resArr
          .filter((it) => it.httpOnly)
          .map(({ name, value }) => `${name}=${value}`)
          .join('; ');

        setObj((pre) => ({
          ...pre,
          original,
          target,
          open: true,
          cookies: cookiesStr,
          cookiesMap: {
            [original]: cookiesStr
          }
        }));
      });
    } else {
      setObj((pre) => ({
        ...pre,
        original,
        target,
        open: true,
        cookiesMap: {}
      }));
    }
  };

  return (
    <>
      <FloatButton
        onClick={() => setVisible(!visible)}
        icon={
          <Tooltip title="Add" placement="left">
            <PlusOutlined />
          </Tooltip>
        }
      />

      <Modal
        onOk={onOk}
        open={visible}
        width={800}
        bodyStyle={{ padding: '20px' }}
        onCancel={() => setVisible(false)}
      >
        <div>
          <div className="flex gap-12">
            <span style={{ width: '70px' }}>url</span>
            <Input onChange={onChange} />
          </div>

          <div className="flex gap-12">
            <span style={{ width: '70px' }}>url</span>
            <span>{obj.target}</span>
          </div>
        </div>
      </Modal>
    </>
  );
};

export default AddProxyModal;
