import { Alert, Button } from 'antd';
import { useRequest } from 'ahooks';

import { getServiceStatus } from '../service';

const App = () => {
  const { error, run } = useRequest(getServiceStatus);

  if (error) {
    return (
      <Alert
        showIcon
        type="error"
        message=""
        className="mb-10"
        action={
          <Button onClick={run} className="ml-30" size="small" type="primary">
            
          </Button>
        }
      />
    );
  }

  return null;
};

export default App;
