import { useCallback, useEffect, useState } from 'react';
import { Input, Button, Space, InputNumber } from 'antd';
import ErrorStatus from './ErrorStatus';
import styled from 'styled-components';
import stripJsonComments from 'strip-json-comments';
import { writeTextToClipboard } from '../../utils/utils';
import useMemoryInput from './hook';
import { Card } from 'antd';

const { TextArea } = Input;

const StyledWrapper = styled.div`
  width: 100%;
  overflow: auto;
  display: grid;
  grid-template-columns: 1fr 600px;
  grid-gap: 20px;

  .header {
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .card-list {
    display: grid;
    grid-gap: 12px;
    overflow: auto;
    grid-template-columns: repeat(2, 1fr);
    height: calc(100vh - 120px);
  }

  .result-card-title {
    display: grid;
    grid-template-columns: 40px auto;
    .right {
      display: flex;
      justify-content: flex-end;
    }
  }
`;

const StyledTextArea = styled(TextArea)`
  border: none;
  height: 100%;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
`;

const placeholder = `[
  /**
   * json, , 
   * [
   *    ["", "English description"],
   *    ["2", "English description second"]
   * ]
   */
]`;

/**
 * @desc json
 */
const getUpperStr = (val: string, maxKeyLength = 5) => {
  const splited = val.toLocaleUpperCase().split(' ');

  return splited
    .slice(0, maxKeyLength || splited.length)
    .join('_')
    .trim();
};

const getResultStr = (arr: [string, string][], maxKeyLength: number): string => {
  const enRes = arr.reduce(
    (tol, [zhStr, enStr]) =>
      (tol += `"${getUpperStr(enStr, maxKeyLength)}": "${enStr}",
`),
    ''
  );

  const zhRes = arr.reduce(
    (tol, [zhStr, enStr]) =>
      (tol += `"${getUpperStr(enStr, maxKeyLength)}": "${zhStr}",
`),
    ''
  );

  return `
${enRes}

${zhRes}
  `;
};

const getResultArr = (arr: [string, string][], maxKeyLength: number): string[][] => {
  const res = arr.map(([zhStr, enStr]) => [getUpperStr(enStr, maxKeyLength), zhStr]);
  const sortedRes = res.sort((a, b) => b[1].length - a[1].length);

  return sortedRes;
};

export default function I18nTransform() {
  /**  */
  const [error, setError] = useState('');

  const { sourceData, codeInput, setCodeInput, setSourceData } = useMemoryInput();
  /** key  */
  const [maxKeyLength, setMaxKeyLength] = useState(5);
  /** i18n-result */
  const [resultData, setResultData] = useState('');
  /** i18n-result-array */
  const [resultArr, setResultArr] = useState<string[][]>([]);
  /** code-result */
  const [codeResult, setCodeResult] = useState('');

  /** i18n */
  const updateI18InputResult = useCallback(() => {
    try {
      const excludesComments = stripJsonComments(sourceData);
      const arr = JSON.parse(excludesComments);
      const resultString = getResultStr(arr, maxKeyLength);
      const resultArr = getResultArr(arr, maxKeyLength);

      setResultData(resultString);
      setResultArr(resultArr);
      setError('');
    } catch (error: any) {
      console.log({ error });
      setError(error?.message);
    }
  }, [maxKeyLength, sourceData]);

  /** input onChange */
  const onChange = useCallback(
    (e: any) => {
      const newValue = e.target.value;
      console.log('newValue', newValue);
      setSourceData(newValue);
    },
    [setSourceData]
  );

  /**  */
  const updateCodeResult = useCallback(() => {
    let res = resultArr.reduce(
      (tol, [enKey, zhText]) => tol.replaceAll(zhText, `t('${enKey}')`),
      codeInput
    );

    if (!res.includes('react-i18next')) {
      res = `
        import { useTranslation } from 'react-i18next';
        import { ICF_TRANSLATE_NAME } from '../constants';
        const { t } = useTranslation(ICF_TRANSLATE_NAME);

        ${res}
      `;
    }

    setCodeResult(res);
  }, [resultArr, codeInput]);

  /**  */
  const onCodeChange = (e: any) => {
    const newValue = e.target.value;
    setCodeInput(newValue);
  };

  /** copy result */
  const onCopyResult = () => {
    writeTextToClipboard(resultData);
  };

  useEffect(() => {
    updateI18InputResult();
  }, [maxKeyLength, updateI18InputResult, sourceData]);

  useEffect(() => {
    updateCodeResult();
  }, [codeInput, updateCodeResult]);

  return (
    <StyledWrapper>
      <div className="grid gap-12">
        <Card
          size="small"
          title={
            <div className="result-card-title">
              <span></span>
              <div className="right">
                <ErrorStatus error={error} />
              </div>
            </div>
          }
        >
          <StyledTextArea
            id="source"
            rows={10}
            onChange={onChange}
            value={sourceData}
            placeholder={placeholder}
          />
        </Card>

        <Card
          size="small"
          title={
            <div className="result-card-title">
              <span>i18 local-json</span>
              <div className="right">
                <Space size="small">
                  <span>key length:</span>
                  <InputNumber
                    min={1}
                    max={10}
                    size="small"
                    type="number"
                    value={maxKeyLength}
                    onChange={(val) => setMaxKeyLength(val as any)}
                  />
                  <Button onClick={onCopyResult} size="small">
                    Copy Result
                  </Button>
                </Space>
              </div>
            </div>
          }
        >
          <StyledTextArea rows={10} value={resultData} />
        </Card>

        <Card size="small" title="">
          <StyledTextArea
            rows={10}
            value={codeInput}
            onChange={onCodeChange}
            placeholder={''}
          />
        </Card>
      </div>

      <Card size="small" title="">
        <StyledTextArea rows={10} value={codeResult} placeholder={''} />
      </Card>
    </StyledWrapper>
  );
}
